export default {
	data() {
		return {
			uid: 1,
			modalName: null,
			colorList: ["red", "orange", "yellow", "olive", "green", "cyan", "blue", "purple", "mauve", "pink"],
			bigDays: [],
			selectedId: -1,
		}
	},
	methods: {
		//获取用户id
		getUid: function() {
			const _this = this;
			//获取id
			wx.login({
				success(res) {
					if (res.code) {
						//发起网络请求
						const secret = res.code;
						let uinfo = {};
						wx.getUserInfo({
							success: function(res) {
								uinfo = res.userInfo;
								uni.request({
									method: "GET",
									url: "http://localhost:8080/api/UserManage",
									header: {
										"content-type": "application/json"
									},
									data: {
										code: secret,
										gender: uinfo.gender,
										nickname: uinfo.nickName,
										avaUrl: uinfo.avatarUrl
									},
									success: (res) => {
										_this.uid = res.data.uid;
										_this.getBigDays();
										// console.log(_this.uid);
									},
									fail: (res) => {
										// console.log(res);
										console.log('登录失败!');
									}
								})
							}
						});
					} else {
						console.log('登录失败！' + res.errMsg)
					}
				}
			});

		},

		//获取bigdays
		getBigDays: function() {
			//根据id获取清单
			// console.log(this.uid);
			uni.request({
				method: 'GET',
				url: 'http://localhost:8080/api/bigDay/view',
				data: {
					uid: this.uid,
				},
				success: (res) => {
					if (res.data.err == 0) {
						// console.log(res);
						this.bigDays = res.data.data;
					} else {
						console.log(res);
						console.log("Bad request.");
					}

				},
				fail: () => {
					console.log("Request failed.");
				}
			})
		},
		createBigDay: function() {
			uni.navigateTo({
				animationType: "slide-in-right",
				url: "./createBigDay"
			})
		},
		showModal: function(e) {
			this.modalName = "DialogModal";
		},

		hideModal: function(e) {
			this.modalName = null
		},
		del: function(dateId) {
			this.showModal();
			this.selectedId = dateId;
		},
		comfirmEdit: function() {
			console.log("edit: ", this.selectedId);
			this.hideModal();
			uni.navigateTo({
				url:'./editeBigDay?uid='+this.uid+'&id='+this.selectedId
			})
		},
		comfirmDel: function() {
			const _this = this;
			// console.log("delete: ", this.selectedId);
			uni.request({
				method: 'GET',
				url: 'http://localhost:8080/api/bigDay/delete',
				data: {
					uid: _this.uid,
					id: _this.selectedId
				},
				success: (res) => {
					if (res.data.err == 1){
						this.getUid();
					}
				}
			})
			this.hideModal();
		},
	},
	mounted: function() {
		this.$nextTick(function() {
			this.getUid();
		})
	},
	onPullDownRefresh() {
		//监听下拉刷新动作的执行方法，每次手动下拉刷新都会执行一次
		this.getUid();
	}
}
